import { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAuthStore } from '../stores';
import { Button } from '../components/ui/Button';
import { Input } from '../components/ui/Input';
import { Spinner } from '../components/ui/Spinner';
import { FileCode2 } from 'lucide-react';

export function RegisterPage() {
    const navigate = useNavigate();
    const { register } = useAuthStore();
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [error, setError] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');

        if (password !== confirmPassword) {
            setError('Mt khu xc nhn khng khp');
            return;
        }

        if (password.length < 6) {
            setError('Mt khu phi c t nht 6 k t');
            return;
        }

        setIsLoading(true);

        try {
            await register(email, password, name);
            navigate('/dashboard');
        } catch (err) {
            setError(err instanceof Error ? err.message : 'ng k tht bi');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="flex min-h-screen items-center justify-center bg-gradient-to-br from-background via-background to-primary/5 p-8">
            <div className="w-full max-w-md space-y-8">
                <div className="text-center">
                    <div className="flex items-center gap-2 justify-center mb-6">
                        <FileCode2 className="h-10 w-10 text-primary" />
                        <span className="text-3xl font-bold bg-gradient-to-r from-primary to-primary/60 bg-clip-text text-transparent">
                            HeyTeX
                        </span>
                    </div>
                    <h2 className="text-2xl font-bold text-foreground">To ti khon mi</h2>
                    <p className="mt-2 text-muted-foreground">
                        Bt u hnh trnh bin tp ti liu chuyn nghip
                    </p>
                </div>

                <div className="glass rounded-2xl p-8 shadow-xl">
                    <form onSubmit={handleSubmit} className="space-y-5">
                        {error && (
                            <div className="p-3 rounded-lg bg-destructive/10 text-destructive text-sm">
                                {error}
                            </div>
                        )}

                        <Input
                            label="H v tn"
                            type="text"
                            placeholder="Nguyn Vn A"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            required
                            autoComplete="name"
                        />

                        <Input
                            label="Email"
                            type="email"
                            placeholder="email@example.com"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            required
                            autoComplete="email"
                        />

                        <Input
                            label="Mt khu"
                            type="password"
                            placeholder=""
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            required
                            autoComplete="new-password"
                        />

                        <Input
                            label="Xc nhn mt khu"
                            type="password"
                            placeholder=""
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            required
                            autoComplete="new-password"
                        />

                        <Button type="submit" className="w-full" disabled={isLoading}>
                            {isLoading ? <Spinner size="sm" /> : 'ng k'}
                        </Button>
                    </form>
                </div>

                <p className="text-center text-sm text-muted-foreground">
                     c ti khon?{' '}
                    <Link to="/login" className="text-primary hover:underline font-medium">
                        ng nhp
                    </Link>
                </p>
            </div>
        </div>
    );
}
